import React, { useCallback, useRef } from 'react';
import { useTranslation } from 'react-i18next';
import { usePermissions } from '../hooks/usePermissions.ts';
import ConsultationTracker from '../components/ConsultationTracker.tsx';
import ConsultationList, { ConsultationListRef } from '../components/ConsultationList.tsx';

const Consultations: React.FC = () => {
  const { t } = useTranslation();
  const { hasPermission } = usePermissions();
  const consultationListRef = useRef<ConsultationListRef>(null);

  // Callback-Funktion fr das Neuladen der Beratungsliste (normale Aktionen)
  const handleConsultationChange = useCallback((consultation?: any) => {
    if (consultation && consultation.id) {
      // Wenn eine Consultation bergeben wurde, verwenden wir den imperativen Handle
      // um die Consultation zur Liste hinzuzufgen oder zu aktualisieren
      // Prfe: Wenn endTime vorhanden, ist es ein Update (Stop), sonst ein Add (Start)
      if (consultation.endTime) {
        // Consultation wurde beendet - Update
        consultationListRef.current?.updateConsultation?.(consultation);
      } else {
        // Consultation wurde gestartet - Add
        consultationListRef.current?.addConsultation?.(consultation);
      }
    } else {
      // Fallback: Vollstndiges Reload wenn keine Consultation bergeben
      consultationListRef.current?.refresh();
    }
  }, []);

  // Callback-Funktion fr das Starten einer Beratung (Filter wechseln)
  const handleConsultationStarted = useCallback((clientName: string) => {
    // Triggert ein Neuladen der Beratungsliste UND wechselt zum Client-Filter
    consultationListRef.current?.refresh();
    consultationListRef.current?.activateClientFilter?.(clientName);
  }, []);

  // Prfe Berechtigungen
  if (!hasPermission('consultations', 'read')) {
    return (
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-md p-4">
          <div className="flex">
            <div className="ml-3">
              <h3 className="text-sm font-medium text-red-800 dark:text-red-200">
                {t('consultations.noPermission')}
              </h3>
              <div className="mt-2 text-sm text-red-700 dark:text-red-300">
                <p>{t('consultations.noPermissionMessage')}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen dark:bg-gray-900">
      <div className="max-w-7xl mx-auto py-0 px-2 -mt-6 sm:-mt-3 lg:-mt-3 sm:px-4 lg:px-6">
        <div className="space-y-6">
        {/* Consultation Tracker */}
        <ConsultationTracker 
          onConsultationChange={handleConsultationChange}
          onConsultationStarted={handleConsultationStarted}
        />

        {/* Consultation List */}
        <ConsultationList 
          ref={consultationListRef}
          onConsultationChange={handleConsultationChange}
        />
        </div>
      </div>
    </div>
  );
};

export default Consultations; 